.navbar {
    margin-bottom: 20px;
}

.footer-content {
    margin-top: 30px;
}

.card {
    margin-bottom: 20px;
}

.admin-menu,
.course-configuration-menu {
    .nav {
        li.active {
            border: 1px dotted white;
        }

        .nav-item {
            a {
                color: white;
            }

            a:hover {
                color: black;
            }

            a.disabled {
                color: rgba(255, 255, 255, 0.40);
            }
        }
    }

    background-color: #05a69d;
    margin-top: -20px;
    margin-bottom: 30px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    height: 50px;
}

.selected-bg {
    background-color: #80808014;
}

.text-niagara {
    @extend .text-primary;
    color: $niagara!important;
}

.dropdown-with-icon .dropdown-item > i {
    width: 25px;
    text-align: center;
}

.cursor-default {
    cursor: default;
}

.cursor-pointer {
    cursor: pointer;
}

.blue-500 {
    color: $blue-500;
}

.z-index-backdrop {
    z-index: $zindex-modal-backdrop;
}
